.coaches {
  padding-top: 51px;
  padding-bottom: 106px;
  position: relative;

  @include vp-1199 {
    padding-top: 45px;
    padding-bottom: 101px;
  }

  @include vp-767 {
    padding-top: 7px;
    padding-bottom: 71px;
  }
}

.coaches__container {
  width: 1366px;
  margin: 0 auto;
  position: relative;

  @include vp-1199 {
    width: 100%;
    padding: 0 45px;
  }

  @include vp-767 {
    // padding: 0 15px;
  }
}

.coaches__title {
  margin-top: 0;
  margin-bottom: 48px;
  margin-left: 105px;

  letter-spacing: 1px;
  font-size: 54px;
  line-height: 64px;

  @include vp-1199 {
    text-align: center;
    margin-left: 0;
    margin-bottom: 31px;

    font-size: 42px;
    line-height: 50px;
  }

  @include vp-767 {
    margin-bottom: 42px;

    font-size: 32px;
    line-height: 38px;
  }
}

.coaches__slider {
  width: 1200px;

  @include vp-1199 {
    width: 604px;
  }

  @include vp-767 {
    width: 250px;
  }
}

.coaches__slider-button-prev,
.coaches__slider-button-next {
  width: 56px;
  height: 56px;
  top: 254px;

  color: $color-default-dark;
  border-radius: 50%;

  background-color: $color-transparent;

  &::after {
    font-size: 0;
  }

  @include hover-focus {
    border: 1px solid $color-default-dark;
  }

  @include active {
    border: 1px solid $color-default-dark;
    background-color: $color-default-dark;
    color: $color-default-white;
  }

  @include vp-1199 {
    top: 222px;
  }

  @include vp-767 {
    top: 213px;
  }
}

.coaches__slider-button-prev {
  left: 30px;

  @include vp-1199 {
    left: 26px;
  }

  @include vp-767 {
    left: -5px;
  }
}

.coaches__slider-button-next {
  right: 30px;

  @include vp-1199 {
    right: 26px;
  }

  @include vp-767 {
    right: -5px;
  }
}

.coaches__slider-button-prev svg {
  position: absolute;
  z-index: 10;
  display: flex;
  margin-right: 3px;
}

.coaches__slider-button-next svg {
  position: absolute;
  z-index: 10;
  display: flex;
  margin-left: 3px;
}

.coaches__list {
  display: flex;
  justify-content: center;
  column-gap: 40px;
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;

  list-style-type: none;
}

